import React from 'react';
import './Me.scss';
const Me = props => {


  return (
    <div className="me">
      <div className={props.theme === 'light' ? "text_me li" : "text_me dr"}>
        <h1>Hello, <span>I'm Matthew.</span></h1>
        <p>I am a self-taught programmer. 
          I'm coding in <span className="js">JavaScript</span> and <span className="rc">React</span>, learning new things, 
          such as React Native, Nodejs etc.</p>
      </div>
    </div>
  );
}

export default Me;
